const platform = uni.getSystemInfoSync().platform;

/**
 * LoadMore 加载更多
 * @description 用于列表中，做滚动加载使用，展示 loading 的各种状态
 * @tutorial https://ext.dcloud.net.cn/plugin?id=29
 * @property {String} status = [more|loading|noMore] loading 的状态
 * 	@value more loading前
 * 	@value loading loading中
 * 	@value noMore 没有更多了
 * @property {Number} iconSize 指定图标大小
 * @property {Boolean} iconSize = [true|false] 是否显示 loading 图标
 * @property {String} iconType = [snow|circle|auto] 指定图标样式
 * 	@value snow ios雪花加载样式
 * 	@value circle 安卓唤醒加载样式
 * 	@value auto 根据平台自动选择加载样式
 * @property {String} color 图标和文字颜色
 * @property {Object} contentText 各状态文字说明，值为：{contentdown: "上拉显示更多",contentrefresh: "正在加载...",contentnomore: "没有更多数据了"}
 * @event {Function} clickLoadMore 点击加载更多时触发
 */
export default {
    name: "UniLoadMore",
    props: {
        status: {
            // 上拉的状态：more-loading前；loading-loading中；noMore-没有更多了
            type: String,
            default: "more"
        },
        showIcon: {
            type: Boolean,
            default: true
        },
        iconType: {
            type: String,
            default: "auto"
        },
        iconSize: {
            type: Number,
            default: 24
        },
        color: {
            type: String,
            default: "#777777"
        },
        contentText: {
            type: Object,
            default() {
                return {
                    contentdown: "上拉显示更多",
                    contentrefresh: "正在加载...",
                    contentnomore: "没有更多了"
                };
            }
        }
    },
    data() {
        return {
            webviewHide: false,
            platform: platform
        };
    },

    computed: {
        iconSnowWidth() {
            return (Math.floor(this.iconSize / 24) || 1) * 2;
        }
    },

    mounted() {











    },
    methods: {
        onClick() {
            this.$emit("clickLoadMore", {
                detail: {
                    status: this.status
                }
            });
        }
    }
};
